.imprint_flex{
  display: flex;
  justify-content: center;
  height: 600px;
  width: 1920px;

}

#impressum {
position: sticky;
right: 232px;
    display: flex;
    flex-direction: column;
    align-items: center;

font-size: 16px;
font-weight: bold;
 
text-shadow: 1px 1px 4px black;
color: rgba(245, 245, 245, 0.869);
margin-top: 200px;
font-family: 'Montserrat';
}
body{
    display: flex;
    justify-content: center;
margin: 0;
padding: 0;
background-color: black;
  height: 70vh;
}

.back{
    rotate: 270deg;
    margin-top: 24px;
}

.language{
  margin-top: 14px;

}

.language button {
  border-radius: 64px;
  cursor: pointer;
}
.language button:hover {
  background-color: #41BB80;
}

@media (max-width:1175px) {
  #impressum{
    font-size: 11px;
    margin-top: 360px;
  }
  
}

@media (max-width:803px) {
  #impressum{    right: 60px;
  }
  }

@media (max-width:600px) {
#impressum{

}
}

@media (max-width:400px) {
  #impressum{

    right: 80px;
  }
  }